"use client";
import { useState, useEffect } from "react";
import SearchAndSelect from "./SearchAndSelect";
import { User, Menu, X, Bell, ChevronDown } from "lucide-react";

const Header = () => {
  const [mobileMenuOpen, setMobileMenuOpen] = useState(false);
  const [scrolled, setScrolled] = useState(false);

  // Handle scroll effect for header
  useEffect(() => {
    const handleScroll = () => {
      const isScrolled = window.scrollY > 10;
      if (isScrolled !== scrolled) {
        setScrolled(isScrolled);
      }
    };

    window.addEventListener("scroll", handleScroll);
    return () => {
      window.removeEventListener("scroll", handleScroll);
    };
  }, [scrolled]);

  const toggleMobileMenu = () => {
    setMobileMenuOpen(!mobileMenuOpen);
  };

  return (
    <>
      <div 
        className={`fixed top-0 left-0 right-0 z-10 ${
          scrolled 
            ? "bg-gray-900/95 shadow-lg shadow-cyan-900/5 py-2" 
            : "bg-transparent py-4"
        } transition-all duration-300`}
      >
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="flex items-center justify-between h-16">
            {/* Logo Area */}
            <div className="flex items-center gap-2">
              <div className="flex items-center space-x-1">
                <svg className="w-8 h-8" viewBox="0 0 32 32" fill="none" xmlns="http://www.w3.org/2000/svg">
                  <rect x="2" y="10" width="4" height="12" rx="1" fill="#0e7490" />
                  <rect x="6" y="8" width="4" height="16" rx="1" fill="#06b6d4" />
                  <rect x="10" y="13" width="12" height="6" rx="1" fill="#0e7490" />
                  <rect x="22" y="8" width="4" height="16" rx="1" fill="#06b6d4" />
                  <rect x="26" y="10" width="4" height="12" rx="1" fill="#0e7490" />
                </svg>
                <p className="bg-gradient-to-r from-cyan-400 to-cyan-600 inline-block 
                  text-transparent bg-clip-text font-bold text-xl font-sans tracking-tight">MetaFit</p>
              </div>
            </div>



            <div className="hidden lg:flex items-center space-x-6">
              <SearchAndSelect />
              <button className="text-gray-300 hover:text-cyan-400 transition-colors duration-200">
                <Bell className="h-5 w-5 stroke-[url(#gradient)]" />
              </button>
              <button className="flex items-center p-1.5 rounded-full bg-gray-800/40 hover:bg-gray-800/80 
                transition-colors duration-200 group">
                <User className="h-5 w-5 stroke-[url(#gradient)]" />
              </button>
            </div>
            
            <div className="flex lg:hidden items-center space-x-4">
              <div className="sm:block md:block lg:hidden">
                <SearchAndSelect />
              </div>
              <button 
                className="text-cyan-400 focus:outline-none p-2 rounded-lg hover:bg-gray-800/40 
                  transition-colors duration-200"
                onClick={toggleMobileMenu}
                aria-label={mobileMenuOpen ? "Close menu" : "Open menu"}
              >
                {mobileMenuOpen ? 
                  <X className="w-6 h-6" /> : 
                  <Menu className="w-6 h-6" />
                }
              </button>
            </div>
          </div>
        </div>
      </div>

      {/* Mobile Menu */}
      {mobileMenuOpen && (
        <div className="lg:hidden fixed inset-0 z-50 bg-gray-900/98 backdrop-blur-md">
          <div className="pt-20 pb-6 px-4 max-w-md mx-auto">
            <div className="flex flex-col space-y-6">
              <div className="flex justify-between items-center mb-4">
                <div className="flex items-center gap-2">
                  <svg className="w-8 h-8" viewBox="0 0 32 32" fill="none" xmlns="http://www.w3.org/2000/svg">
                    <rect x="2" y="10" width="4" height="12" rx="1" fill="#0e7490" />
                    <rect x="6" y="8" width="4" height="16" rx="1" fill="#06b6d4" />
                    <rect x="10" y="13" width="12" height="6" rx="1" fill="#0e7490" />
                    <rect x="22" y="8" width="4" height="16" rx="1" fill="#06b6d4" />
                    <rect x="26" y="10" width="4" height="12" rx="1" fill="#0e7490" />
                  </svg>
                  <p className="bg-gradient-to-r from-cyan-400 to-cyan-600 inline-block 
                    text-transparent bg-clip-text font-bold text-xl font-sans">MetaFit</p>
                </div>
                <button 
                  className="text-cyan-400 p-2 rounded-lg hover:bg-gray-800/40 transition-colors duration-200"
                  onClick={toggleMobileMenu}
                  aria-label="Close menu"
                >
                  <X className="w-6 h-6" />
                </button>
              </div>
              
              <div className="mt-8 pt-6 border-t border-gray-700/30">
                <div className="flex justify-between items-center">
                  <div className="flex items-center space-x-3">
                    <div className="p-2 rounded-full bg-gray-800/70">
                      <User className="w-6 h-6 stroke-[url(#gradient)]" />
                    </div>
                    <div>
                      <p className="text-gray-200 font-medium">Guest User</p>
                      <p className="text-gray-400 text-sm">Sign in to track progress</p>
                    </div>
                  </div>
                  <Bell className="h-6 w-6 stroke-[url(#gradient)]" />
                </div>
              </div>
            </div>
          </div>
        </div>
      )}

      <svg width="0" height="0">
        <defs>
          <linearGradient id="gradient" x1="0%" y1="0%" x2="100%" y2="0%">
            <stop offset="0%" stopColor="#06b6d4" />
            <stop offset="100%" stopColor="#0e7490" />
          </linearGradient>
        </defs>
      </svg>
    </>
  );
};

export default Header;